import React, { useMemo } from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import { HStack } from '@chakra-ui/react'

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Link from '../../../components/Link'
import Source from '../../../components/Source'
import Container from '../../../components/Container'
import theme, { Media, responsive } from '../../../components/ThemeProvider/theme'
import useCsv from '../../useCsv'
import Info from './Info'

const Invest = () => {
  const { data } = useCsv('/channel.csv')
  const applications = useMemo(() => [
    {
      location: '保管銀行',
      name: '彰化銀行'
    },
    {
      location: '銷售機構',
      name: data ? (
        <HStack flexWrap="wrap" justify="left" divider={<p>、</p>}>
          {data.map((d, i) => <Link isExternal href={d.url} key={i}>{d.name}</Link>)}
        </HStack>
      ) : '',
    },
  ], [data])
  return (
    <Box bg="rgba(240,244,244,0.71)" pt={theme.headerHeight}>
      <Container>
        <Text.Bold textAlign="center" color="custom.blue" fontSize={responsive('1.5em', '2.5em')}>長期投資更有利</Text.Bold>
        <Text textAlign="center" fontSize={responsive('0.875em', '1.5em')}>持有N類型超過三年，遞延手續費為0%</Text>
        <Box mt="3.125em" px={responsive('0', '22.5%')}>
          <Media lessThan="tablet">
            <StaticImage src="fundchart.png" alt="遞延手續費圖" />
          </Media>
          <Media greaterThanOrEqual="tablet">
            <StaticImage src="fundchart_desk.png" alt="遞延手續費圖" />
          </Media>
        </Box>
        <Source note borderColor="custom.borderBlue">
          <Text.Bold color="custom.orange">凱基投信N類型投資未達(含)1、2、3年，遞延手續費率分別為3%、2%、1%，於買回時計收，滿3年後即零遞延手續費，且不收取分銷費，請詳參基金公開說明書。</Text.Bold>
        </Source>
        <Text.Bold textAlign="center" color="custom.blue" fontSize={responsive('1.5em', '2.5em')}>申購請洽</Text.Bold>
        <Flex alignItems="stretch" mt="1em" justifyContent="center" flexDirection={responsive('column', 'row')}>
          {applications.map(({ location, name }, k) => (
            <Flex
              flexDirection="column"
              borderRadius="1em"
              overflow="hidden"
              fontSize={responsive('1em', '1.25em')}
              width={responsive('100%', '45%')}
              mt={k && responsive('1.5rem', 0)}
              mx={responsive(0, '0.5em')}
              key={k}
            >
              <Box
                py="1em"
                textAlign="center"
                color="white"
                bg="custom.lightBlue"
              >
                {location}
              </Box>
              <Flex
                flex={1}
                py="1em"
                alignItems="center"
                justifyContent="center"
                bg="white"
                whiteSpace="pre-wrap"
              >
                <Box width="15em" textAlign="center">{name}</Box>
              </Flex>
            </Flex>
          ))}
        </Flex>
        <Info />
      </Container>
    </Box>
  )
}

export default Invest
